.toggle--checkbox {
  display: none;
}

.container--toggle {
  display: grid;
  place-items: center;
  min-height: 60px;
  position: relative;
  grid-area: toggle;
  margin: 20px;
}

/* background */
.toggle--label {
  width: 100px;
  height: 54px;
  background: #6b7abb;
  background: var(--indigo-color);
  border-radius: 100px;
  border: 3px solid #5d6baa;
  border: 3px solid var(--indigo-border);
  display: -webkit-flex;
  display: flex;
  position: relative;
  transition: all 350ms ease-in;
}

.toggle--checkbox:checked + .toggle--label {
  background: #96dcee;
  background: var(--blue-color);
  border-color: #72cce3;
  border-color: var(--blue-border);
}

/* sun and moon */
.toggle--checkbox:checked + .toggle--label:before {
  -webkit-animation-name: reverse;
          animation-name: reverse;
  -webkit-animation-duration: 350ms;
          animation-duration: 350ms;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  transition: all 360ms ease-in;
  background: #fffaa8;
  background: var(--yellow-background);
  border-color: #f5eb71;
  border-color: var(--yellow-border);
}

.toggle--label:before {
  -webkit-animation-name: switch;
          animation-name: switch;
  -webkit-animation-duration: 350ms;
          animation-duration: 350ms;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  content: "";
  width: 40px;
  height: 40px;
  border: 3px solid #e8e8ea;
  border: 3px solid var(--gray-border);
  top: 1px;
  left: 3px;
  position: absolute;
  border-radius: 40px;
  background: white;
}

/* moon dimples */
.toggle--label:after {
  transition-delay: 0ms;
  transition: all 250ms ease-in;
  position: absolute;
  content: "";
  box-shadow: #e8e8ea -14px 0 0 1px, #e8e8ea -22px 12px 0 -1px;
  box-shadow: var(--gray-dots) -14px 0 0 1px, var(--gray-dots) -22px 12px 0 -1px;
  left: 80px;
  top: 10px;
  width: 6px;
  height: 6px;
  background: transparent;
  border-radius: 50%;
  opacity: 1;
}

.toggle--checkbox:checked + .toggle--label:after {
  transition-delay: 50ms;
  opacity: 0;
}

/* clouds */
.toggle--checkbox:checked + .toggle--label .toggle--label-background {
  width: 5px;
  left: 65px;
  top: 22px;
}

.toggle--checkbox:checked + .toggle--label .toggle--label-background:before {
  top: -4px;
  left: -5px;
  width: 20px;
  height: 5px;
}

.toggle--checkbox:checked + .toggle--label .toggle--label-background:after {
  top: 3px;
  width: 20px;
  height: 5px;
  left: -10px;
}

/* stars */
.toggle--label-background {
  border-radius: 5px;
  position: relative;
  background: white;
  left: 32px;
  width: 4px;
  transition: all 150ms ease-in;
  top: 25px;
  height: 4px;
}

.toggle--label-background:before {
  content: "";
  position: absolute;
  width: 4px;
  height: 4px;
  top: -15px;
  border-radius: 5px;
  background: white;
  left: -20px;
  transition: all 150ms ease-in;
}

.toggle--label-background:after {
  content: "";
  position: absolute;
  width: 4px;
  height: 4px;
  left: -20px;
  top: 10px;
  border-radius: 5px;
  background: white;
  transition: all 150ms ease-in;
}

